import {useBlockProps} from '@wordpress/block-editor';
import {TextHighlight} from '@wordpress/components';

export default function Save({attributes}) {
    const {
        heading,
        headingTag,
        tocAdded,
        textAlign,
        desktopHeadingSize,
        tabletHeadingSize,
        mobileHeadingSize,
        highlight,
        highLightColor,
        highLightBg,
        darkHighLightColor,
        darkHighLightBg
    } = attributes;

    var headingClasses = `gb-highlight`;
    if (!tocAdded) {
        headingClasses += ` none-toc`;
    }
    if (textAlign) {
        headingClasses += ` has-text-align-${textAlign}`;
    }

    const WrapperTag = headingTag || 'h2';

    const blockProps = useBlockProps.save({
            className: headingClasses,
            style: {
                ...(desktopHeadingSize && {'--desktop-heading-size': `${desktopHeadingSize}px`}),
                ...(tabletHeadingSize && {'--tablet-heading-size': `${tabletHeadingSize}px`}),
                ...(mobileHeadingSize && {'--mobile-heading-size': `${mobileHeadingSize}px`}),
                ...(highLightColor && {'--highlight-color': highLightColor}),
                ...(darkHighLightColor && {'--dark-highlight-color': darkHighLightColor}),
                ...(highLightBg && {'--highlight-bg': highLightBg}),
                ...(darkHighLightBg && {'--dark-highlight-bg': darkHighLightBg}),
            }
        }
    );

    return (
        <div {...blockProps}>
            <WrapperTag className="gb-heading">
                <TextHighlight
                    text={heading}
                    highlight={highlight}
                />
            </WrapperTag>
        </div>
    );
}
