import {__} from '@wordpress/i18n';
import {
    AlignmentControl,
    InspectorControls,
    PanelColorSettings,
    BlockControls,
    useBlockProps
}           from '@wordpress/block-editor';
import {
    Dashicon,
    PanelBody,
    ToggleControl,
    SelectControl,
    TextareaControl,
    TextControl,
    TextHighlight
}           from '@wordpress/components';

export default function Edit(props) {
    const {attributes, setAttributes} = props;
    const {
        heading,
        highlight,
        headingTag,
        textAlign,
        tocAdded,
        desktopHeadingSize,
        tabletHeadingSize,
        mobileHeadingSize,
        highLightColor,
        highLightBg,
        darkHighLightColor,
        darkHighLightBg
    } = attributes;

    const setAttribute = (name, value) => {
        setAttributes({[name]: value});
    };

    var headingClasses = `gb-highlight gb-heading`;
    if (!tocAdded) {
        headingClasses += ` none-toc`;
    }
    if (textAlign) {
        headingClasses += ` has-text-align-${textAlign}`;
    }

    const blockProps = useBlockProps({
        className: headingClasses,
        style: {
            ...(desktopHeadingSize && {'--desktop-heading-size': `${desktopHeadingSize}px`}),
            ...(tabletHeadingSize && {'--tablet-heading-size': `${tabletHeadingSize}px`}),
            ...(mobileHeadingSize && {'--mobile-heading-size': `${mobileHeadingSize}px`}),
            ...(highLightColor && {'--highlight-color': highLightColor}),
            ...(darkHighLightColor && {'--dark-highlight-color': darkHighLightColor}),
            ...(highLightBg && {'--highlight-bg': highLightBg}),
            ...(darkHighLightBg && {'--dark-highlight-bg': darkHighLightBg}),
        }
    });

    const headingTagOptions = [
        {label: __('H2'), value: 'h2'},
        {label: __('H3'), value: 'h3'},
        {label: __('H4'), value: 'h4'},
        {label: __('H5'), value: 'h5'},
        {label: __('H6'), value: 'h6'},
        {label: __('p'), value: 'p'},
        {label: __('div'), value: 'div'},
    ];

    const WrapperTag = headingTag || 'div';

    return (
        <WrapperTag {...blockProps}>
            <BlockControls group="block">
                <AlignmentControl
                    value={textAlign}
                    onChange={(nextAlign) => {
                        setAttributes({textAlign: nextAlign});
                    }}
                />
            </BlockControls>
            <InspectorControls>
                <PanelBody title={__('General')} initialOpen={true}>
                    <TextareaControl
                        label={__('Content')}
                        value={heading}
                        onChange={(value) => setAttribute('heading', value)}
                        placeholder="Add your content here..."
                    />
                    <TextareaControl
                        label={__('Highlight Text')}
                        value={highlight}
                        onChange={(value) => setAttribute('highlight', value)}
                        help={__('Input text to search for and highlight within the text. Case insensitive, multiple matches')}
                    />
                    <SelectControl
                        label={__('Content Tag')}
                        value={headingTag}
                        options={headingTagOptions}
                        onChange={(value) => setAttribute('headingTag', value)}
                    />
                    <ToggleControl
                        label={__('Table of Content Included')}
                        checked={tocAdded}
                        onChange={(value) => setAttribute('tocAdded', value)}
                    />
                </PanelBody>
            </InspectorControls>
            <InspectorControls group="styles">
                <PanelBody title={__('Style')} initialOpen={true}>
                    <label className="rb-control-label">{__('Font Size')}</label>
                    <div className="responsive-settings">
                        <div>
                            <label>
                                <Dashicon icon="desktop"/>
                                {__('Desktop')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="18"
                                value={desktopHeadingSize}
                                onChange={(value) => setAttribute('desktopHeadingSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="tablet"/>
                                {__('Tablet')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="17"
                                value={tabletHeadingSize}
                                onChange={(value) => setAttribute('tabletHeadingSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="smartphone"/>
                                {__('Mobile')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="16"
                                value={mobileHeadingSize}
                                onChange={(value) => setAttribute('mobileHeadingSize', value)}
                            />
                        </div>
                    </div>
                    <PanelColorSettings
                        title={__('Color')}
                        colorSettings={[
                            {
                                value: highLightColor,
                                onChange: (value) => setAttribute('highLightColor', value),
                                label: __('Highlight Color'),
                            },
                            {
                                value: highLightBg,
                                onChange: (value) => setAttribute('highLightBg', value),
                                label: __('Highlight Background'),
                            },
                        ]}
                    />
                    <PanelColorSettings
                        title={__('Dark Mode - Color')}
                        colorSettings={[
                            {
                                value: darkHighLightColor,
                                onChange: (value) => setAttribute('darkHighLightColor', value),
                                label: __('Highlight Color'),
                            },
                            {
                                value: darkHighLightBg,
                                onChange: (value) => setAttribute('darkHighLightBg', value),
                                label: __('Highlight Background'),
                            },
                        ]}
                    />
                </PanelBody>
            </InspectorControls>
            <TextHighlight
                text={heading}
                highlight={highlight}
            />
        </WrapperTag>
    );
}
